import React, { useState } from 'react';
import './AddFormCharacteristicItem.css'; //   CSS 

import trashIcon from '../../assets/deleteImage.svg';

function AddFormCharacteristicItem({ addCharacteristic }) {
  //    
  const [title, setTitle] = useState('');
  const [value, setValue] = useState('');
  const [unit, setUnit] = useState('');

  //       
  const handleChange = (e, field) => {
    const inputValue = e.target.value;
    switch (field) {
      case 'title':
        setTitle(inputValue);
        break;
      case 'value':
        setValue(inputValue);
        break;
      case 'unit':
        setUnit(inputValue);
        break;
      default:
        break;
    }
  };

  //       ""
  const handleAdd = () => {
    const newCharacteristic = {
      title: title,
      value: value,
      unit: unit
    };
    addCharacteristic(newCharacteristic);
    //   
    setTitle('');
    setValue('');
    setUnit('');
  };

  //       
  const handleDelete = () => {
    //     
    console.log('Characteristic deleted');
  };

  return (
    <div className="addCharacteristicItem">
        <div className="addCharacteristicItem__title">
                <input
                    type="text"
                    className="addCharacteristicItem__input"
                    placeholder="Title"
                    value={title}
                    style={{ width: "264px" }}
                    onChange={(e) => handleChange(e, 'title')}
                />
            </div>
            <div>
                <div className="addCharacteristicItem__value">
                    <input
                        type="text"
                        className="addCharacteristicItem__input"
                        placeholder="Value"
                        value={value}
                        style={{ width: "100px" }}
                        onChange={(e) => handleChange(e, 'value')}
                    />
                <div className="addCharacteristicItem-unit">
                    <input
                        type="text"
                        className="addCharacteristicItem__input"
                        placeholder="Unit"
                        value={unit}
                        style={{ width: "100px" }}
                        onChange={(e) => handleChange(e, 'unit')}
                        />
                </div>
            </div>
        </div>
        <div className='addCharacteristicItem_btn'>
            <button type="button" className="addCharacteristicItem__submit" onClick={handleAdd}></button>
            <div className="addCharacteristicItem__delete">
                {/*      */}
                <img src={trashIcon} alt="trashIcon" onClick={handleDelete}/>
            </div>
        </div>
    </div>
  );
}

export default AddFormCharacteristicItem;
