import React, { useState } from 'react'
import InputUI from '../../UI/InputUI/InputUI';
import BtnGenerate from '../../UI/BtnGenerate/BtnGenerate';
import question from "../../assets/question.svg"

import './styles/InputNameForm.css'

import Yandex from '../../api/Yandex'

function InputNameForm({setNameProduct, isFormat, setIsFormat, nameProduct, setItem}) {

  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  return (
    <div className='form'>
        <InputUI 
        isFormat={isFormat} 
        setIsFormat={setIsFormat}
        width="100%" 
        placeHolder="  ..." 
        setNameProduct={setNameProduct}/>
        {isFormat === "typo" && (
          <div className='typoFormat'>
            <p className='typoFormat__title'>   :</p>
            <div className='typoFormat__text'> </div>
          </div>
        )}
        <div className='format' >
            <p className='format__text' onClick={Yandex.fetchCharacteristics}>  </p>
            <img src={question} alt="question" className='format__icon' onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}/>
            {isHovered && 
              <div className='format__window'>
                - 
              </div>
            }
        </div>
        {isFormat === 'toxic' && (
          <div className='toxicFormat'>       !   .</div>
        )}
        <div className='form__btn'>
            <BtnGenerate nameProduct={nameProduct} setItem={setItem}></BtnGenerate>
        </div>
      </div>
  )
}

export default InputNameForm